.cardlist{
    width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
    margin-top: 50px;
}

.card{
    padding: 30px;
    background-color: skyblue;
    border-radius: 10px;
    border: 1px solid black;
    transition: transform 0.25s ease-out;
      -moz-osx-font-smoothning: grayscale;
    text-align: center;

}
.card:hover{
    transform: scale(1.04);
}

.header{
   text-align: center;
}


.footer{
    position: relative;
    bottom: -10px;
    left: 45%;
    color: white;
    margin-top: 30px;
}

.edituser{
    background-color: skyblue;
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 600px;
    margin: 50px auto;
    text-align: center;
    box-shadow: 0 2px 5px rgba(0,0,0,0.8);
    border-radius: 20px;
}
.edit{
    margin: 20px;
    padding: 10px;
}
.edituser button{
    padding: 10px;
    width: 400px ;
    margin: 10px auto;
    background-color: black;
    color: blanchedalmond;
    border-radius: 10px ;
    
}
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: linear-gradient(to left, rgba(7, 27, 82, 1) 0%,
            rgba(0, 128, 128, 1) 100%);
    overflow-x: hidden;
}

.searchbox{
    /* background-color: violet; */
    padding: 10px 20px;
    padding-right: 100px;
}
.user-searchwrap{
    text-align: center;
    margin-top: 30px;
}


.editsingle{
   padding: 5px 20px;
   margin-top: 10px;
}
